<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process208"></a>

<p><br></p>
<p><span class="rvts17">不可能的恋人（</span><span class="rvts50">Unlikely Lovers</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:708,Y:393</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：阿尔米亚（</span><span class="rvts20">Almira</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">帮助阿尔米亚杀掉死亡先知和他的黑环手下，解除天堂高地的诅咒</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1267.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">阿尔米亚的小屋位置比较偏僻，位于整个死神海岸的东部。这里由于受到了死亡先知的诅咒，只要踏入该区域，就会遭到火流星的袭击，并且身上自带无法消除掉的被诅咒</span><span class="rvts20">BUFF</span><span class="rvts21">。</span>
<br><span class="rvts21">由于天堂高地区域比较支离破碎，因此并没有完整的通往此处的道路，中间需要使用传送，跳跃等技能才可以。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1268.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">死亡先知位于阿尔米亚小屋不远处的一处断桥口，死亡先知还有</span><span class="rvts20">4</span><span class="rvts21">个黑环手下跟班儿，因此并不是特别好打，但是如果队伍中角色等级高的话，会非常容易。</span>
<br><span class="rvts21">杀掉死亡先知后向阿尔米亚汇报，她会通过亲吻你（她是个魅媚），使你获得额外的源力点。另外，杀掉死亡先知可以平息死亡高地的永久诅咒，使万物重见天日。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



